@font-face {
  font-display: swap;
  font-family: "Inter-Regular";
  src: url("../fonts/Inter-Regular.woff2") format("woff2"), url("../fonts/Inter-Regular.ttf") format("truetype"), url("../fonts/Inter-Regular.otf") format("opentype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Inter-SemiBold";
  src: url("../fonts/Inter-SemiBold.woff2") format("woff2"), url("../fonts/Inter-SemiBold.ttf") format("truetype"), url("../fonts/Inter-SemiBold.otf") format("opentype");
  font-weight: 600;
  font-style: normal;
}
@font-face {
  font-display: swap;
  font-family: "Caveat-Bold";
  src: url("../fonts/Caveat-Bold.ttf") format("truetype");
  font-weight: bold;
  font-style: normal;
}
body, html {
  background: var(--white);
  color: var(--black);
  font-family: var(--font-family);
  font-size: var(--c-font-size);
  letter-spacing: 0;
  line-height: 1.4;
  font-weight: 400;
  margin: 0 auto;
  -webkit-text-size-adjust: 100%;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
*:first-child {
  margin-top: 0 !important;
}
*:last-child:not(.popup-cart) {
  margin-bottom: 0 !important;
}

a {
  text-decoration: none;
  transition: all ease-in-out 0.2s;
}

html {
  --c-padding: 0;
  --c-font-size: 14px;
  --cols: 12;
  --gutter: 20px;
  --column: calc((100% - 20px * (12 - 1)) / 12);
  --w-content: 960px;
  --h-header: 48px;
  --font-family: "Inter-Regular", sans-serif;
  --font-family-b: "Inter-SemiBold", sans-serif;
  --font-family-c: "Caveat-Bold", sans-serif;
  --desctop-h1h2-font-size: 52px;
  --desctop-h1h2-line-height: 116%;
  --desctop-h1h2-font-weight: 600;
  --desctop-h3-font-size: 32px;
  --desctop-h3-line-height: 36px;
  --desctop-h3-font-weight: 600;
  --desctop-body-s-sb-font-size: 13px;
  --desctop-body-s-sb-line-height: 140%;
  --desctop-body-s-sb-font-weight: 600;
  --desctop-body-s-reg-font-size: 13px;
  --desctop-body-s-reg-line-height: 140%;
  --desctop-body-s-reg-font-weight: 400;
  --desctop-body-m-sb-font-size: 17px;
  --desctop-body-m-sb-line-height: 22px;
  --desctop-body-m-sb-font-weight: 600;
  --desctop-body-l-sb-font-size: 22px;
  --desctop-body-l-sb-line-height: 28px;
  --desctop-body-l-sb-font-weight: 600;
  --desctop-cta-s-font-size: 13px;
  --desctop-cta-s-line-height: 120%;
  --desctop-cta-s-font-weight: 400;
  --desctop-cta-s-link-font-size: 13px;
  --desctop-cta-s-link-line-height: 120%;
  --desctop-cta-s-link-font-weight: 400;
  --desctop-sta-m-font-size: 15px;
  --desctop-sta-m-line-height: 120%;
  --desctop-sta-m-font-weight: 400;
  --desctop-cta-m-link-font-size: 15px;
  --desctop-cta-m-link-line-height: 120%;
  --desctop-cta-m-link-font-weight: 400;
  --desctop-textaccedent-font-size: 24px;
  --desctop-textaccedent-line-height: 25px;
  --desctop-textaccedent-font-weight: 700;
  --desctop-list-font-size: 13px;
  --desctop-list-line-height: 120%;
  --desctop-list-font-weight: 400;
  --mob-h1h2-font-size: 38px;
  --mob-h1h2-line-height: 42px;
  --mob-h1h2-font-weight: 600;
  --mob-h3-font-size: 26px;
  --mob-h3-line-height: 30px;
  --mob-h3-font-weight: 600;
  --mob-cta-m-font-size: 15px;
  --mob-cta-m-line-height: 20px;
  --mob-cta-m-font-weight: 400;
  --mob-cta-m-semibold-font-size: 15px;
  --mob-cta-m-semibold-line-height: 20px;
  --mob-cta-m-semibold-font-weight: 600;
  --white: #fff;
  --black: #282827;
  --gray: #747473;
  --gray-2: #929292;
  --gray-3: #d4d4d4;
  --line-light: #f5f5f5;
  --bg-light: #fafafa;
  --bg-light-2: #f9f9f9;
  --yellow-main: #f8ca02;
  --yellow-secondary: #ffdf65;
  --orange: #ffc165;
  --orange-2: #f1711c;
  --green: #56e264;
  --pro-grey-pro: #a8a8a8;
  --pro-pro-lines: #232322;
  --pro-lines-bg-pro: #151515;
  --successful: #49aa53;
  --violet: #7165ff;
  --error: #e64832;
  --toner: rgba(0, 0, 0, 0.25);
  --toner-160: rgba(0, 0, 0, 0.16);
  --gradient-1: linear-gradient(
      45deg,
      #FFE581 0%,
      #FFC165 100%
  );
  --gradient-2: linear-gradient(rgb(15 15 15 / 0%), rgb(15 15 15 / 100%));
  --gradient-3: linear-gradient(
      90deg,
      #C4C4C4 100%,
      #C4C4C4 49%,
      #C4C4C4 0%
  );
}
@media (max-width: 640px) {
  html {
    --c-padding: 20px;
  }
}

.overflow {
  overflow: hidden;
}

.hidden {
  display: none !important;
}
@media (max-width: 640px) {
  .hidden--mob {
    display: none !important;
  }
}
@media (min-width: 640px) and (max-width: 1200px) {
  .hidden--tab {
    display: none !important;
  }
}
@media (min-width: 1200px) {
  .hidden--desktop {
    display: none !important;
  }
}

.section {
  width: 100%;
  max-width: 100%;
  min-width: 320px;
  margin: 0 auto;
  position: relative;
}
.section--first {
  padding-top: calc(52px + var(--h-header));
}
.section--shop {
  padding-top: calc(var(--h-header) * 2);
}
.section__wrapper {
  max-width: var(--w-content);
  margin: 0 auto;
  padding: 0 var(--c-padding);
  box-sizing: border-box;
  z-index: 2;
}
@media (max-width: 450px) {
  .section__wrapper {
    padding: 20px;
  }
}
@media (max-width: 640px) {
  .section__wrapper {
    padding: 0 20px;
  }
}
@media (max-width: 1200px) {
  .section__wrapper {
    padding: 0 var(--c-padding);
  }
}
@media (max-width: 1500px) {
  .section__wrapper {
    padding: 0 var(--c-padding);
  }
}
@media (min-width: 320px) {
  .section__wrapper {
    width: 100%;
    padding: 0 20px;
  }
}
@media (max-width: 2560px) {
  .section__wrapper {
    width: 100%;
    padding: 0 var(--c-padding);
  }
}

.flex {
  display: flex;
}
.flex.wrap {
  flex-wrap: wrap;
}
.flex.row {
  flex-direction: row;
}
.flex.col {
  flex-direction: column;
}
.flex > * {
  flex: 1 1 auto;
  margin-top: 0;
}

.media {
  display: flex;
  overflow: hidden;
  position: relative;
}
.media--bg {
  background: var(--bg-light);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.media--bg img, .media--bg video, .media--bg iframe {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.media--img {
  background: var(--bg-light-2);
}

.fly-image {
  position: absolute;
  z-index: 1000;
  pointer-events: none;
  transition: transform 1000ms ease-in-out, opacity 1000ms ease-in-out;
  will-change: transform, opacity;
  width: max-content;
  height: 100vh;
  object-fit: contain;
}

.align-right {
  display: flex;
  justify-content: "flex-end";
  align-items: center;
}

@media (min-width: 640px) {
  .mobile-only {
    display: none !important;
  }
}

@media (min-width: 1200px) {
  .mobile-plus-tablet {
    display: none !important;
  }
}

@media (max-width: 640px) {
  .desktop-plus-tablet {
    display: none !important;
  }
}

.button {
  position: relative;
  display: flex;
  align-content: center;
  font-weight: 400;
  font-size: 13px;
  line-height: 120%;
  text-decoration: none;
  border-radius: 16px;
  padding: 8px 10px 8px 10px;
  width: max-content;
  outline: none;
  border-width: 1px;
  border-style: solid;
  cursor: pointer;
  transition: all linear 0.2s;
}
.button.disabled, .button[disabled] {
  pointer-events: none;
  opacity: 0.25;
}
.button--white, .button--black {
  background: rgba(0, 0, 0, 0);
}
.button--white {
  border-color: var(--white);
  color: var(--white);
}
.button--white:hover {
  color: var(--yellow-main);
  border-color: var(--yellow-main);
}
.button--black {
  color: var(--black);
  border-color: var(--black);
}
.button--black:hover {
  color: var(--yellow-main);
  border-color: var(--yellow-main);
}
.button--yellow {
  color: var(--black);
  background: var(--yellow-secondary);
  border-color: transparent;
}
.button--yellow:hover {
  background: var(--yellow-main);
}
.button--big {
  border-radius: 24px;
  padding: 14px 24px;
}

.language {
  position: relative;
  margin-right: auto;
}
.language__link {
  display: flex;
  align-items: center;
  min-width: 40px;
  font-weight: 400;
  font-size: 13px;
  line-height: 120%;
  color: var(--white);
}
.language__link:focus, .language__link:hover {
  color: var(--yellow-main);
}
.language__link:focus {
  color: var(--yellow-main);
}
.language__link:focus .language__icon {
  transform: rotate(90deg);
}
.language__icon {
  display: inline-block;
  margin-left: 12px;
  width: 6px;
  height: 4px;
  background: var(--yellow-secondary);
  mask-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='6' height='4' viewBox='0 0 6 4' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M3.32822 3.46667C3.12822 3.73333 2.72822 3.73333 2.52822 3.46667L0.528223 0.799999C0.281009 0.470381 0.5162 -5.32739e-07 0.928223 -4.96719e-07L4.92822 -1.47027e-07C5.34025 -1.11007e-07 5.57544 0.470382 5.32822 0.8L3.32822 3.46667Z' fill='%23FFDF65'/%3e%3c/svg%3e");
  mask-size: contain;
  mask-repeat: no-repeat;
  transition: transform 0.3s ease;
}

.promo-links {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  width: 100%;
  gap: 16px;
  padding-top: 32px;
}
.promo-links__item {
  display: flex;
  justify-content: "flex-start";
  align-items: "flex-start";
  flex-direction: column;
}
.promo-links__item h3 {
  font-weight: 600;
  font-size: 13px;
  line-height: 140%;
  color: var(--gray);
  margin-bottom: 8px;
}
.promo-links__item ul {
  list-style: none;
}
.promo-links__item ul li {
  margin-bottom: 6px;
}
.promo-links__item ul li a {
  display: flex;
  justify-content: "flex-start";
  align-items: "center";
  color: var(--white);
}
.promo-links__item ul li a .tag {
  margin-left: 5px;
}
.promo-links__item ul li a:hover {
  color: var(--yellow-secondary);
}
.promo-links__item ul li.current-menu-item a {
  color: var(--yellow-main);
}
.promo-links__mt {
  margin-top: 16px;
}
.promo-links__directions ul li a, .promo-links__catalog ul li a, .promo-links__company ul li a {
  font-weight: 400;
  font-size: 13px;
  line-height: 140%;
}
.promo-links__social {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 16px;
}
.promo-links__social a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: auto;
}
.promo-links__social a svg {
  width: 16px;
  height: 16px;
  min-width: 100%;
  fill: var(--yellow-secondary);
  transition: all ease-in-out 0.2s;
}
.promo-links__social a:hover svg {
  fill: var(--white);
}

.tag {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  height: 18px;
  font-family: var(--font-family-b);
  font-size: 9px;
  border-width: 1px;
  border-style: solid;
  border-radius: 4px;
  padding: 0 8px;
  white-space: nowrap;
}
.tag--discount {
  color: var(--white);
  border-color: var(--orange-2);
  background: var(--orange-2);
}
.tag--gift {
  color: var(--orange);
  border-color: var(--orange);
}
.tag--soon {
  color: var(--violet);
  border-color: var(--violet);
}
.tag--new {
  color: var(--green);
  border-color: var(--green);
}

h1, h2,
.h1h2 {
  font-family: var(--font-family-b);
  font-size: var(--desctop-h1h2-font-size);
  line-height: var(--desctop-h1h2-line-height);
  font-weight: var(--desctop-h1h2-font-weight);
  letter-spacing: -0.2px;
}

h3,
.h3 {
  font-family: var(--font-family-b);
  font-size: var(--desctop-h3-font-size);
  line-height: var(--desctop-h3-line-height);
  font-weight: var(--desctop-h3-font-weight);
  letter-spacing: -0.2px;
}

.body-s-sb, textarea, input {
  font-family: var(--font-family-b);
  font-size: var(--desctop-body-s-sb-font-size);
  line-height: var(--desctop-body-s-sb-line-height);
  font-weight: var(--desctop-body-s-sb-font-weight);
  letter-spacing: 0;
}

.body-s-reg {
  font-family: var(--font-family);
  font-size: var(--desctop-body-s-reg-font-size);
  line-height: var(--desctop-body-s-reg-line-height);
  font-weight: var(--desctop-body-s-reg-font-weight);
  letter-spacing: 0;
}

.body-m-sb {
  font-family: var(--font-family-b);
  font-size: var(--desctop-body-m-sb-font-size);
  line-height: var(--desctop-body-m-sb-line-height);
  font-weight: var(--desctop-body-m-sb-font-weight);
  letter-spacing: 0;
}

.body-l-sb {
  font-family: var(--font-family-b);
  font-size: var(--desctop-body-l-sb-font-size);
  line-height: var(--desctop-body-l-sb-line-height);
  font-weight: var(--desctop-body-l-sb-font-weight);
  letter-spacing: 0;
}

.cta-s {
  font-family: var(--font-family);
  font-size: var(--desctop-cta-s-font-size);
  line-height: var(--desctop-cta-s-line-height);
  font-weight: var(--desctop-cta-s-font-weight);
  letter-spacing: 0;
}

.cta-s-link {
  font-family: var(--font-family);
  font-size: var(--desctop-cta-s-link-font-size);
  line-height: var(--desctop-cta-s-link-line-height);
  font-weight: var(--desctop-cta-s-link-font-weight);
  letter-spacing: 0;
}

.sta-m, .button--big {
  font-family: var(--font-family);
  font-size: var(--desctop-sta-m-font-size);
  line-height: var(--desctop-sta-m-line-height);
  font-weight: var(--desctop-sta-m-font-weight);
  letter-spacing: 0;
}

.cta-m-link {
  font-family: var(--font-family);
  font-size: var(--desctop-cta-m-link-font-size);
  line-height: var(--desctop-cta-m-link-line-height);
  font-weight: var(--desctop-cta-m-link-font-weight);
  letter-spacing: 0;
}

.textaccedent {
  font-family: var(--font-family-c);
  font-size: var(--desctop-textaccedent-font-size);
  line-height: var(--desctop-textaccedent-line-height);
  font-weight: var(--desctop-textaccedent-font-weight);
  letter-spacing: 0;
}

.list {
  margin-left: 22px;
}
.list li {
  font-family: var(--font-family);
  font-size: var(--desctop-list-font-size);
  line-height: var(--desctop-list-line-height);
  font-weight: var(--desctop-list-font-weight);
  letter-spacing: 0;
  margin-bottom: 9px;
}

.inp-wrp .error {
  font-weight: 400;
  font-size: 10px;
  line-height: 190%;
  color: var(--error);
}

.form-description {
  text-align: left;
  font-size: 10px;
  line-height: 140%;
  font-weight: 400;
  color: var(--black);
}
.form-description a {
  color: var(--black);
  text-decoration: underline;
}
.form-description a:hover {
  text-decoration: none;
}

textarea, input {
  color: var(--black);
  border: none;
  outline: none;
  font-family: var(--font-family-b);
  caret-color: var(--yellow-main);
  background-color: transparent;
  border-bottom-width: 1px;
  border-bottom-style: solid;
  border-bottom-color: var(--gray-3);
}
textarea::-webkit-input-placeholder, input::-webkit-input-placeholder {
  color: var(--gray);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:-moz-placeholder, input:-moz-placeholder {
  color: var(--gray);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea::-moz-placeholder, input::-moz-placeholder {
  color: var(--gray);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:-ms-input-placeholder, input:-ms-input-placeholder {
  color: var(--gray);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:focus, input:focus {
  color: var(--black);
  border-bottom-color: var(--gray-3);
}
textarea.required, input.required {
  color: var(--error);
  border-bottom-color: var(--error);
}
textarea.required::-webkit-input-placeholder, input.required::-webkit-input-placeholder {
  color: var(--error);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea.required:-moz-placeholder, input.required:-moz-placeholder {
  color: var(--error);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea.required::-moz-placeholder, input.required::-moz-placeholder {
  color: var(--error);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea.required:-ms-input-placeholder, input.required:-ms-input-placeholder {
  color: var(--error);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:disabled, input:disabled, textarea:read-only, input:read-only {
  border-bottom-color: rgba(212, 212, 212, 0.32);
  cursor: default;
}
textarea:disabled::-webkit-input-placeholder, input:disabled::-webkit-input-placeholder, textarea:read-only::-webkit-input-placeholder, input:read-only::-webkit-input-placeholder {
  color: rgba(116, 116, 115, 0.32);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:disabled:-moz-placeholder, input:disabled:-moz-placeholder, textarea:read-only:-moz-placeholder, input:read-only:-moz-placeholder {
  color: rgba(116, 116, 115, 0.32);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:disabled::-moz-placeholder, input:disabled::-moz-placeholder, textarea:read-only::-moz-placeholder, input:read-only::-moz-placeholder {
  color: rgba(116, 116, 115, 0.32);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}
textarea:disabled:-ms-input-placeholder, input:disabled:-ms-input-placeholder, textarea:read-only:-ms-input-placeholder, input:read-only:-ms-input-placeholder {
  color: rgba(116, 116, 115, 0.32);
  font-family: var(--font-family-b);
  font-weight: 600;
  font-size: 13px;
}

input {
  width: 100%;
  padding-bottom: 8px;
}
input[type=checkbox] {
  display: flex;
  width: 16px;
  height: 16px;
  border: 1px solid var(--gray-3);
  box-sizing: border-box;
  border-radius: 2px;
  outline: none;
  appearance: none;
  padding: 0;
}
input[type=checkbox]:checked {
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='8' height='7' viewBox='0 0 8 7' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M2.53496 5.82187C2.53496 5.82187 0.407977 3.59613 0.255084 3.43651C-0.0850281 3.08075 -0.0850281 2.50184 0.255084 2.14638C0.596063 1.78971 1.1475 1.78971 1.48848 2.14638L2.86547 3.58672C3.0367 3.76582 3.31458 3.76459 3.48436 3.58458L6.50782 0.376444C6.84764 0.0158296 7.40286 0.0133887 7.74557 0.37189C8.08306 0.724905 8.0851 1.30141 7.74992 1.65716C4.46659 5.14168 3.84307 5.82157 3.84307 5.82157C3.4826 6.19859 2.89571 6.19891 2.53496 5.82187Z' fill='%23F8CA02'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: 3px 4px;
}

.dropdown > .dropdown__item > .switcher, .form-switch__label {
  display: flex;
  justify-content: flex-start;
