}
.dialogs .popup.text-popup p {
  font-weight: 400;
}
.dialogs .popup .close {
  position: absolute;
  right: 24px;
  top: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 24px;
  height: 24px;
  border-radius: 100%;
  background-color: #9C9C9C;
  cursor: pointer;
